wrappers\";\n\n/* ------------------------------------ *\\\n    $TEXT\n\\* ------------------------------------ */\n@import \"objects.text\";\n@import \"objects.buttons\";\n\n/* ------------------------------------ *\\\n    $COMPONENTS\n\\* ------------------------------------ */\n@import \"components.blocks\";\n@import \"components.cards\";\n@import \"components.heros\";\n@import \"components.sections\";\n@import \"components.forms\";\n\n/* ------------------------------------ *\\\n    $OBJECTS\n\\* ------------------------------------ */\n@import \"objects.icons\";\n@import \"objects.lists\";\n@import \"objects.navs\";\n@import \"objects.media\";\n\n/* ------------------------------------ *\\\n    $PAGE STRUCTURE\n\\* ------------------------------------ */\n@import \"module.article\";\n@import \"module.gallery\";\n@import \"module.footer\";\n@import \"module.header\";\n@import \"module.main\";\n\n/* ------------------------------------ *\\\n    $MODIFIERS\n\\* ------------------------------------ */\n@import \"modifier.animations\";\n@import \"modifier.colors\";\n@import \"modifier.display\";\n@import \"modifier.spacing\";\n\n/* ------------------------------------ *\\\n    $VENDORS\n\\* ------------------------------------ */\n\n/* ------------------------------------ *\\\n    $TRUMPS\n\\* ------------------------------------ */\n@import \"trumps.helper-classes\";\n","/* ------------------------------------ *\\\n    $VARIABLES\n\\* ------------------------------------ */\n\n/**\n * Grid & Baseline Setup\n */\n// Global\n$max-width: 1200px;\n$max-width-xl: 1600px;\n\n// Grid\n$grid-columns: 16;\n$col-width: 65;\n$gutter: 20;\n\n/**\n * Theme Colors\n */\n\n// Neutrals\n$c-white: #fff;\n$c-gray--light: #f0f0f0;\n$c-gray: #666;\n$c-gray--dark: #c0c1c5;\n$c-black: #000;\n\n$c-primary: #fbc617;\n$c-secondary: #ef4438;\n$c-tertiary: #d1d628;\n\n/**\n * Default Colors\n */\n$c-error: #f00;\n$c-valid: #089e00;\n$c-warning: #fff664;\n$c-information: #000db5;\n$c-overlay: